<template>
<!--    20:03-->
    <div class="about">
        <h1>首页</h1>
<!--        <v-btn-->
<!--                color="info"-->
<!--                @click="this.getScore"-->
<!--        >-->
<!--            &lt;!&ndash;              @click="this.getScore"&ndash;&gt;-->

<!--        </v-btn>-->
<!--        <v-btn-->
<!--                color="info"-->
<!--                @click="this.setScore"-->
<!--        >发送一本率-->

<!--        </v-btn>-->
<!--        <v-btn-->
<!--                color="primary"-->
<!--                dark-->
<!--                class="mb-2"-->
<!--                @click="dialog_show=true"-->
<!--                icon-->
<!--&lt;!&ndash;        >&ndash;&gt;-->

<!--            <v-icon>mdi-account</v-icon>-->
<!--        </v-btn>-->
<!--        1  这是一个弹窗       >-->
        <v-dialog
                v-model="dialog_show"
                persistent

                max-width="500px"
        >
            <v-card>
                <v-card-title>
                </v-card-title>

                <v-card-text>
                    <v-container>
                        <v-row>
                            <v-col

                                    cols="12"
                                    sm="6"
                                    md="4"
                            >
                                <v-text-field
                                        :rules="RequireRules"
                                        v-model="inputScores[0]"
                                        label="语文"
                                ></v-text-field>
                            </v-col>
                            <v-col

                                    cols="12"
                                    sm="6"
                                    md="4"
                            >
                                <v-text-field
                                        :rules="RequireRules"
                                        v-model="inputScores[1]"
                                        label="数学"
                                ></v-text-field>
                            </v-col>
                            <v-col

                                    cols="12"
                                    sm="6"
                                    md="4"
                            >
                                <v-text-field
                                        :rules="RequireRules"
                                        v-model="inputScores[2]"
                                        label="英语"
                                ></v-text-field>
                            </v-col>
                            <v-col

                                    cols="12"
                                    sm="6"
                                    md="4"
                            >
                                <v-text-field
                                        :rules="RequireRules"
                                        v-model="inputScores[3]"
                                        label="综合"
                                ></v-text-field>
                            </v-col>
                        </v-row>
                    </v-container>
                </v-card-text>

                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                            color="blue darken-1"
                            text
                            @click="dialog_show=false"
                    >
                        Cancel
                    </v-btn>

                    <v-btn
                            color="blue darken-1"
                            text
                            @click="dialog_save"
                    >
                        Save
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <!--    第一栏布局：高考成绩、模拟服务-->
        <v-row style="margin-top: 2vh; margin-left: 4vh; width: 100%">

            <v-col width="20vh">
                <v-btn
                        color="primary"
                        style="width:100%;margin-bottom: 1vh;"
                        @click="dialog_show=true"
                        @>{{table_label}}</v-btn>
                <v-card>
                    <v-simple-table>
                        <template v-slot:default>
                            <thead>
                            <tr>
                                <th class="text-center"
                                >
                                    高考分数
                                </th>
                                <th class="text-center">
                                    {{isStudent?"分数":"市均分"}}
                                </th>
                            </tr>
                            </thead>
                            <tbody>
                            <tr
                                    v-for="item in SubjectSocres"
                                    :key="item.name"
                            >
                                <td class="text-center">{{ item.name }}</td>
                                <td class="text-center blue--text">{{ item.score }}</td>
                            </tr>
                            </tbody>

                        </template>
                    </v-simple-table>
                </v-card>
            </v-col>

            <v-col width="60vh">
                <v-card width="50vh">
                    <v-card-title class="green white--text">志愿服务</v-card-title>

                    <v-img
                            height="80"
                            src="data:image/jpeg;base64,"
                    ></v-img>

                    <v-list dense>
                        <v-list-item-group
                                v-model="selectedItem"
                                color="primary"
                        >
                            <v-list-item
                                    v-for="(item, i) in items"
                                    :key="i"
                                    link
                                    :to="item.link"
                            >
                                <v-list-item-icon>
                                    <v-icon v-text="item.icon"></v-icon>
                                </v-list-item-icon>
                                <v-list-item-content >
                                    <v-list-item-title v-text="item.text"></v-list-item-title>
                                </v-list-item-content>
                            </v-list-item>
                        </v-list-item-group>
                    </v-list>

                </v-card>
            </v-col>
        </v-row>

        <!--    第二栏布局:时间段鱼骨图、图片展示-->
        <v-row style="margin-top: 2vh; margin-left: 4vh; width: 100%">

            <!--      鱼骨图-->
            <v-row style="max-width: 45%">
                <v-timeline align-top :dense="true">
                    <v-timeline-item
                            v-for="(item, i) in timeline"
                            :key="i"
                            :color="item.color"
                            :icon="item.icon"
                            fill-dot
                    >
                        <v-card :color="item.color" dark>
                            <v-card-title class="title pt-3 pb-3">
                                <h3 class="title">{{ item.title }}</h3>
                            </v-card-title>
                            <v-card-text class="white text--primary">
                                <p class="pt-3 body-1 mb-0">
                                    {{ item.text }}
                                </p>
                            </v-card-text>
                        </v-card>
                    </v-timeline-item>
                </v-timeline>
            </v-row>
            <!--      图片轮播展示-->
            <v-row style="max-width: 40%; margin-left: 5vh">
                <v-card width="100%">
                    <v-toolbar
                            color="green"
                            dark

                    >
                        <v-app-bar-nav-icon></v-app-bar-nav-icon>
                        <v-toolbar-title>院校风采展示</v-toolbar-title>
                    </v-toolbar>

                    <v-container fluid>
                        <v-row dense>
                            <v-col
                                    v-for="card in cards"
                                    :key="card.title"
                                    :cols="card.flex"
                            >
                                <v-card>
                                    <v-img
                                            :src="card.src"
                                            class="white--text align-end"
                                            gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
                                            height="200px"
                                    >
                                        <v-card-title v-text="card.title"></v-card-title>
                                    </v-img>


                                    <v-card-actions>
                                        <v-spacer></v-spacer>

                                        <v-btn icon>
                                            <v-icon>mdi-heart</v-icon>
                                        </v-btn>

                                        <v-btn
                                                link
                                                @click="ExternalLink(SchoolLink)"
                                                icon>
                                            <v-icon>mdi-bookmark</v-icon>
                                        </v-btn>

                                        <v-btn icon>
                                            <v-icon>mdi-share-variant</v-icon>
                                        </v-btn>
                                    </v-card-actions>
                                </v-card>
                            </v-col>
                        </v-row>
                    </v-container>
                </v-card>
            </v-row>

        </v-row>
        <!--      院校信息展示-->
        <v-col>
            <v-card>
                <v-card-title>报考信息展示</v-card-title>

                <v-row style="margin-top: 2vh; max-width: 100%; margin: 0">
                    <v-item-group
                            v-model="window"
                            class="shrink mr-6"
                            mandatory
                            tag="v-flex"
                    >
                        <v-item
                                v-for="n in length"
                                :key="n"
                                v-slot="{ active, toggle }"
                        >
                            <div>
                                <v-btn
                                        :input-value="active"
                                        icon
                                        @click="toggle"
                                >
                                    <v-icon>mdi-record</v-icon>
                                </v-btn>
                            </div>
                        </v-item>
                    </v-item-group>

                    <v-col>
                        <v-window
                                v-model="window"
                                class="elevation-1"
                                vertical
                        >
                            <v-window-item
                                    v-for="school_inform in SchoolIntroduction"
                                    :key="school_inform.name"

                            >
                                <v-card flat>
                                    <v-card-text>
                                        <v-row
                                                class="mb-4"
                                        >
                                            <v-avatar>
                                                <v-img
                                                        :src="school_inform.badge_url"

                                                >
                                                </v-img>

                                            </v-avatar>
                                            <v-spacer></v-spacer>
                                            <strong class="text-h6">{{ school_inform.name }}</strong>
                                            <v-spacer></v-spacer>
                                            <v-btn icon>
                                                <v-icon>mdi-account</v-icon>
                                            </v-btn>
                                            <br>
                                            {{ school_inform.introduction }}
                                            <br>
                                        </v-row>
                                    </v-card-text>
                                </v-card>
                            </v-window-item>
                        </v-window>
                    </v-col>
                </v-row>
            </v-card>

        </v-col>


    </div>
</template>

<script>
    import axios from "axios";

    export default {
        inject:['reload'],
        //刷新模块
        data() {
            return {
                //表格
                table_label: "",
                //判断表格是否填报
                table_fill:false,
                isStudent: false,
                inputScores: [0,0,0,0],
                SubjectSocres: [
                    {
                        name: '语文',
                        score: 87,
                    },
                    {
                        name: '数学',
                        score: 65,
                    },
                    {
                        name: '外语',
                        score: 56,
                    },
                    {
                        name: '综合',
                        score: 140,
                    },
                ],
                SchoolLink: "https://www.baidu.com",
                ScoreList: [],
                SchoolIntroduction: [
                    {
                        name: "北大风采",
                        introduction: "北京大学（PekingUniversity），简称“北大”，是中华人民共和国教育部直属的全国重点大学，位列“双一流”、“211工程”、“985工程”，入选“学位授权自主审核单位”、“基础学科拔尖学生培养试验计划”、“基础学科招生改革试点”、“高等学校创新能力提升计划”、“高等学校学科创新引智计划”，为九校联盟、松联盟、中国大学校长联谊会、京港大学联盟、亚洲大学联盟、东亚研究型大学协会、国际研究型大学联盟、环太平洋大学联盟、全球大学校长论坛、21世纪学术联盟、东亚四大学论坛、国际公立大学论坛、中俄综合性大学联盟成员。\n",
                        badge_url: "https://upload.wikimedia.org/wikipedia/commons/thumb/f/f4/Peking_University_seal.svg/1200px-Peking_University_seal.svg.png",
                        url: "",
                    },
                    {
                        name: "顶尖清华",
                        introduction: "清华大学（TsinghuaUniversity），简称“清华”，是中华人民共和国教育部直属的全国重点大学，位列国家“双一流”A类、“985工程”、“211工程”，入选“2011计划”、“珠峰计划”、“强基计划”、“111计划”，为九校联盟（C9）、松联盟、中国大学校长联谊会、亚洲大学联盟、环太平洋大学联盟、清华—剑桥—MIT低碳大学联盟成员、中国高层次人才培养和科学技术研究的基地，被誉为“红色工程师的摇篮”。",
                        badge_url: "https://tse1-mm.cn.bing.net/th/id/R-C.4b43eae59b6593d567772d944cd0f2b8?rik=yGGWqRDy2Do83A&riu=http%3a%2f%2fpic18.nipic.com%2f20111220%2f9114815_223214639177_2.jpg&ehk=UBdZOSi91xUXBeqT5avuf01D01PxTpVw%2fz%2f13d3AyVQ%3d&risl=&pid=ImgRaw&r=0",
                        url: "",
                    },
                    {
                        name: "北邮",
                        introduction: "北京邮电大学（Beijing University of Posts andTelecommunications），简称北邮，是中华人民共和国教育部直属、工业和信息化部共建的全国重点大学，位列国家“世界一流学科建设高校”、“211工程”、“985工程优势学科创新平台”，为北京高科大学联盟、中国人工智能教育联席会、CDIO工程教育联盟创始成员，入选国家“111计划”、“2011计划”、中国政府奖学金来华留学生接收院校、教育部第一批卓越工程师教育培养计划高校、国家建设高水平大学公派研究生项目、教育部首批“新工科”研究与实践项目、全国首批深化创新创业教育改革示范高校、一流网络安全学院建设示范项目高校、国家大学生文化素质教育基地、全国邮政行业人才培养基地。\n",
                        badge_url: "https://upload.wikimedia.org/wikipedia/zh/thumb/d/d6/Beijing_University_of_Posts_and_Telecommunications.jpg/205px-Beijing_University_of_Posts_and_Telecommunications.jpg",
                        url: "",
                    },
                ],
                timeline: [
                    {
                        "color": "red lighten-2",
                        "icon": "mdi-star",
                        "title": "6月7日 晚上20：00",
                        "text": "高考结束，高考参考答案可供下载。"
                    },
                    {
                        "color": "purple darken-1",
                        "icon": "mdi-ring",
                        "title": "6月23日 下午13：00",
                        "text": "省教育考试院正式开放成绩查询，系统正式开放报考查询"
                    },
                    {
                        "color": "green lighten-1",
                        "icon": "mdi-balloon",
                        "title": "6月25日 下午2：00",
                        "text": "系统导入一分一段表，更新了院校与招生计划，提供报考咨询"
                    },
                    {
                        "color": "indigo",
                        "icon": "mdi-gift",
                        "title": "6月27日 下午18：00",
                        "text": "《高考成绩分析以及志愿报考》白皮书现已上线平台，可供考生免费下载。"
                    }
                ],
                //志愿服务
                selectedItem: 1,
                items: [
                    {text: '模拟填报', icon: 'mdi-clock',link:'/layout/SchoolPrediction'},
                    {text: '学校估分', icon: 'mdi-account',link:'/layout/ScoreEstimation'},
                    {text: '学校信息', icon: 'mdi-flag',link:'/layout/SchoolInformation'},
                ],

                //报考信息展示
                length: 3,
                window: 0,
                dialog_show: false,
                //用于院校风采展示的图片链接
                //用于院校风采展示的图片链接
                cards: [
                    {
                        title: '厦大',
                        src: 'https://www.gotopku.cn/uploads/image/2016/20161025/20161025103605_66137.jpg',
                        flex: 12
                    },
                    {
                        title: '北航',
                        src: 'https://p2.itc.cn/q_70/images01/20210422/98f899cf963b42c0af56f7fa4f114ec1.jpeg',
                        flex: 6
                    },
                    {
                        title: '北邮',
                        src: 'https://img0.baidu.com/it/u=2970898148,2619628022&fm=26&fmt=auto&gp=0.jpg',
                        flex: 6
                    },
                ],
                //高考分数表格
                RequireRules: [

                ],
                Less150Rules: [
                    v => !!v || '您需要输入分数',
                    v =>{
                        if(v.length>3){
                            return true
                        }
                        return
                    } ,
                ],
                Less300Rules: [
                    v => !!v || '您需要输入分数',
                ],
                ExternalLink(link) {
                    window.location.href = link;
                },
            }
        },
        mounted: function (){

            //身份判断
            if(localStorage.getItem("Identity")=="学生") {
                this.isStudent=true
                // console.log("载入数据")
                this.getScore();
                if(this.table_fill===true) {
                    this.table_label = "您的高考分数 ";
                }
                else{
                    this.table_label="点击填报您的高考分数"
                    this.SubjectSocres[0]["score"]="未填报";
                    this.SubjectSocres[1]["score"]="未填报";
                    this.SubjectSocres[2]["score"]="未填报";
                    this.SubjectSocres[3]["score"]="未填报";
                }
            }
            else {
                this.isStudent=true
                this.table_label="高考各科平均分表"
            }
        },
        methods: {
            dialog_save() {
                var that = this;
                that.dialog_show = false
                //分数发给后端
                that.setScore()
                window.console.log(this.inputScores[0])
                that.SubjectSocres[0]['score'] =this.inputScores[0]
                that.SubjectSocres[1]['score'] = this.inputScores[1]
                that.SubjectSocres[2]['score'] = this.inputScores[2]
                that.SubjectSocres[3]['score'] =  this.inputScores[3]
              location.reload();
            },
            close() {
                var that = this;
                that.dialog_show=false
                //    dialog是窗口的v-model
            },
            setScore: function() {
                var that = this;
                var url = "http://127.0.0.1:9090/student/setScore"

                window.console.log("setScore初始化" + localStorage.getItem("Identity") + localStorage.getItem("userName"));
                if (localStorage.getItem("Identity") === "学生") {
                    axios({
                        url: url,
                        method: 'post',
                        //这里传入变量
                        params: {
                            studentID: localStorage.getItem("userName"),
                            chinese:this.inputScores[0],
                            math:this.inputScores[1],
                            english: this.inputScores[2],
                            science: this.inputScores[3],
                        }
                    })
                        .then(function (response) {
                            if (response.data.code === "0") {
                                alert("高考成绩录入成功");
                                that.table_fill=true
                                this.table_label="您的高考成绩"

                            } else {
                                alert("您的用输入错误，请检查。");
                            }
                        })
                }

            },
            getScore: function () {
                // console.log("初始化")
                var that = this;
                var get_url = "http://127.0.0.1:9090/student/getScore"
                if (localStorage.getItem("Identity") === "学生") {
                    window.console.log("获取载入表格数据")

                    axios({
                        url: get_url,
                        method: 'post',
                        //这里传入变量
                        params: {
                            studentID: localStorage.getItem("userName"),
                        }
                    })
                        .then(function (response) {
                            window.console.log("抓包信息" + JSON.stringify(response.data.data)+"DataCode"+response.data.code)
                            if (response.data.code === "0") {
                                let getList= response.data.data[0];
                                let totalScore=Number(getList['chinese'])+Number(getList['math'])+Number(getList['english'])+Number(getList['science']);
                              localStorage.setItem("Score", totalScore.toString())
                                window.console.log("totalScore"+totalScore+"StudentID"+localStorage.getItem("userName"));
                                if(totalScore===0) {
                                    localStorage.setItem("score","-1")
                                }else{
                                    window.console.log("从数据库中查询到高考分数信息"+getList['chinese']+getList['math']);
                                    that.SubjectSocres[0]['score'] = parseInt(getList['chinese']);
                                    that.SubjectSocres[1]['score'] = parseInt(getList['math']);
                                    that.SubjectSocres[2]['score'] = parseInt(getList['english']);
                                    that.SubjectSocres[3]['score'] = parseInt(getList['science']);

                                    that.table_fill=true;
                                    that.table_label = "您的高考分数 ";
                                }
                            } else if(response.data.code === "1"){
                                alert("未查询到账户");
                                that.table_fill=false;
                                localStorage.setItem("score","-1")
                            }
                        }).catch(function(){

                    })
                }


            },
        },
    }
</script>
